const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			amount: '0.00',
			moneys: ''
		}
	},
	//初始化
	onLoad() {
		
	},
	// 页面显示
	onShow() {
		this.getInfo();
	},
	onShareAppMessage: function () {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/index/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	methods:{
		//获取可提现金额
		getInfo() {
			util.request(api.getCashOut,{},'get').then(res=>{
				if(res.code == 0) {
					this.amount = res.data.amount
				}
			})
		},
		
		//获取提现金额
		money(e) {
			this.moneys = e.detail.value;
		},
		sDigit: function (value) {
			var patrn = /^([1-9]\d*|0)(\.\d*[1-9])?$/;
			if (!patrn.exec(value)) {
		        return false;
		    }else {
		        return true;
		    }
		},
		//提现
		rechargeBtn() {
			if(this.moneys == 0) {
				wx.showToast({
					icon: 'none',
					title: '金额不能为空'
				})
				return false;
			}else if(!this.sDigit(this.moneys)) {
				wx.showToast({
					icon: 'none',
					title: '请输入正确的金额'
				})
				return false;
			}else if(parseInt(this.moneys) > parseInt(this.amount)) {
				wx.showToast({
					icon: 'none',
					title: '输入的金额不能大于可提现的金额'
				})
				return false;
			}
			
			wx.showLoading({
				mask: true,
				title: '提交中...'
			})
			
			util.request(api.cashOutBtn,{amount: this.moneys},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					wx.navigateTo({
						url: '/pages/withdraw/list'
					})
				}else {
					util.showErrorToast(res.msg);
					return false;
				}
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	}
}
